body {
  background-color: #f5f5f5;
  margin: 0;
  font-family: Arial, sans-serif;
}

.header-top {
  background-color: #eaba22;
  padding: 10px 20px;
}

.header-content {
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.header-logo {
  border: 2px solid #333;
  border-radius: 50%;
  width: 60px;
  height: auto;
  margin-right: 15px;
}

.header-title {
  color: #333;
  margin: 0;
  font-size: 24px;
}

.searchbox {
  box-sizing: border-box;
  background-color: #fff;
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

.container {
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  overflow: hidden;
}

.sidebar {
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  padding: 10px;
  box-shadow: 0 2px 5px #0000001a;
}

.main-content {
  box-sizing: border-box;
  background-color: #fff;
  flex: 1;
  padding: 10px;
  overflow-y: auto;
}

#hits {
  width: 100%;
}

.ais-Hits-list {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -5px;
  padding: 0;
  list-style: none;
  display: flex;
}

.ais-Hits-item {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  flex-basis: calc(50% - 10px);
  margin: 5px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 2px 10px #0000001a;
}

.hit {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 10px;
  display: flex;
}

.hit-image {
  object-fit: cover;
  border-radius: 4px;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.hit-content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  font-size: .9em;
  display: flex;
}

.hit-name {
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin-bottom: 5px;
  font-size: .9em;
  display: -webkit-box;
  overflow: hidden;
}

.hit-price {
  font-weight: bold;
}

#pagination {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.ais-Pagination-list {
  padding: 0;
  list-style-type: none;
  display: flex;
}

.ais-Pagination-item {
  margin: 0 5px;
}

.ais-Pagination-link {
  color: #333;
  background-color: #f0f0f0;
  border-radius: 4px;
  padding: 5px 10px;
  text-decoration: none;
}

.ais-Pagination-item--selected .ais-Pagination-link {
  color: #fff;
  background-color: #eaba22;
}

.ais-RefinementList-list {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.ais-RefinementList-item {
  background-color: #f0f0f0;
  border-radius: 20px;
  padding: 5px 10px;
}

.ais-RefinementList-label {
  align-items: center;
  display: flex;
}

.ais-RefinementList-checkbox {
  margin-right: 5px;
}

.ais-RangeSlider {
  margin: 0 10px;
}

.rheostat-horizontal {
  width: calc(100% - 20px);
}

.rheostat-background {
  width: 100%;
}

.rheostat-handle {
  margin-left: -12px;
}

.rheostat-marker {
  margin-left: -1px;
}

@media (width >= 768px) {
  .container {
    flex-direction: row;
    height: calc(100vh - 140px);
  }

  .sidebar {
    flex-shrink: 0;
    width: 20%;
    padding: 20px;
  }

  .ais-Hits-list {
    justify-content: flex-start;
  }

  .ais-Hits-item {
    flex-basis: calc(20% - 10px);
  }

  .hit {
    padding: 15px;
  }

  .ais-RefinementList-list {
    display: block;
  }

  .ais-RefinementList-item {
    background-color: #0000;
    border-radius: 0;
    padding: 5px 0;
  }

  .searchbox {
    padding: 10px 20px;
  }

  .ais-RangeSlider {
    margin: 0;
  }

  .rheostat-horizontal {
    width: 100%;
  }
}
/*# sourceMappingURL=index.9cb193f2.css.map */
